.root {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
  

    li {
      a {
        text-align: center;
        width: 45px;
        line-height: 42px;
        height: 45px;
        border: 1px solid #fe5f1e;
        border-radius: 30px;
        margin-right: 10px;
        cursor: pointer;
        display: inline-block;
        color: #fe5f1e;
  
        &:hover {
          background-color: #fe5f1e;
          color: #fff;
        }
      }
    }
  
    :global {
      .selected {
        a {
          background-color: #fe5f1e;
          color: #fff;
        }
      }
    }
  
    @media (max-width: 400px) {
      display: flex;
      justify-content: center;
    }
  }
  